	module.exports = {"data":{"markdownRemark":{"html":"<h2>Focus Groups</h2>\n<h3>Definitions</h3>\n<ul>\n<li>A group of people who have been brought together to discuss a particular subject in order to solve a problem or suggest ideas. - Cambridge Dictionary<br></li>\n<li>A group of people brought together to give their opinions on a particular issue or product, often for the purpose of market research. -Collins English Dictionary</li>\n</ul>\n<p>Α focus group is a group of people gathered together because of their common characteristics, such as age, gender, occupation, income etc. Focus groups are formed in order for researchers to conduct a market research, for specific demographic groups. <br></p>\n<p>Unlike an interview, where the input comes from a single person, in focus groups we have different answers from various people which provide a more diverse and accurate insight in the market’s needs and wants. Additionally, discussions in focus groups allow people to interact with each other, form new opinions and see different perspectives of the topic in question.</p>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href=\"https://www.thebalance.com/what-is-a-market-research-focus-group-2296907\">https://www.thebalance.com/what-is-a-market-research-focus-group-2296907</a></p>","fields":{"slug":"/user-experience-design/focus-groups/"},"frontmatter":{"title":"Focus Groups"}}},"pathContext":{"slug":"/user-experience-design/focus-groups/"}}

